import React, { Component } from 'react';
import './App.css';
import './FadeInLink.scss'

class FadeInLink extends Component {

    constructor(props) {
        super(props)
        this.state = {
            origionalText: this.props.innerText,
            animationType: this.props.animationType,
            presentSpans: '',
            letterEachDelay: this.props.letterEachDelay || 50,
            animationDelay: this.props.animationDelay || 0
            // animation delay : see FadeInText.scss 
        }
        setTimeout(() => {
            switch (this.state.animationType) {
                case 'fadeInBottom':
                    this.delaySpans();
                    break;
                default:
                    break;
            }
        },
            this.state.animationDelay
        )
    }

    componentDidMount = () => {
        
    }

    delaySpans = () => {
        let presentSpans = []
        let delay = this.state.animationDelay
        let i = 0
        for (const letter of this.state.origionalText.toString()) {
            if (letter !== ' ') {
                presentSpans.push(<span key={this.props.id + '-' + i} style={{ animationDelay: delay + 'ms', MozAnimationDelay: delay + 'ms', WebkitAnimationDelay: delay + 'ms' }}>{letter}</span>)
                delay += this.state.letterEachDelay;
            } else {
                presentSpans.push()
            }
            i += 1
        }
        this.setState({ presentSpans: presentSpans })
    }
    render() {
        return (
            <a id={this.props.id}  className={'cssanimation leFadeInBottom fadein-link'} href={this.props.href || '#'}>{this.state.presentSpans}</a>
        )
    }
}

export default FadeInLink;