import { TimelineMax as Timeline, Power1 } from 'gsap';

const getHomeTimeline = (node, delay, pathname) => {
    const timeline = new Timeline({ paused: true });
    const texts = document.body.querySelector("section").querySelectorAll('h1');
    console.log('getHomeTimeline');
    console.log(pathname);
        timeline
            .from(node, 0, { display: 'none', autoAlpha: 0, delay })
            .staggerFrom(texts, 0.375, { autoAlpha: 0, x: -25, ease: Power1.easeOut }, 0.125);

    return timeline
}

const getDefaultTimeline = (node, delay, pathname) => {
    const timeline = new Timeline({ paused: true });
    const texts = document.body.querySelector("section").querySelectorAll('h1');
    console.log('getDefaultTimeline');

    timeline
        .from(node, 0, { display: 'none', autoAlpha: 0, delay })
        .staggerFrom(texts, 0.75, { autoAlpha: 0, x: -25, ease: Power1.easeOut }, 0.125);

    return timeline
}

const csrollDownTimeLine = (node, delay, pathname) => {
    const timeline = new Timeline({ paused: true });
    const pageWrapper = document.getElementById('pageWrapper')
    console.log(pageWrapper);

    timeline
    .from(node, 0, { display: 'none', autoAlpha: 0, delay })
    .staggerFrom(pageWrapper, 0.75, { autoAlpha: 0, x: -25, ease: Power1.easeOut }, 0.125);

    return timeline
}

const play = (pathname, node, appears, location) => {
    const delay = appears ? 0 : 0.5
    let timeline
    console.log(pathname, node, appears, location);

    if (pathname === '/' && location.hash === '#f'){
        timeline = csrollDownTimeLine(node, delay, pathname)
        console.log('1',location);
        
    }
    else if (pathname === '/') {
        timeline = getDefaultTimeline(node, delay, pathname)
        // window.location.hash = '/'
        console.log('2',location,window.location);
        
    }        
    else {
        timeline = getHomeTimeline(node, delay, pathname)
        console.log('3',location);
        
    }
        

    timeline.play()
}

export default play