import React, { Component } from 'react';
import './App.css';
import SplashScreenCss from './SplashScreen.scss'
import FadeInText from './FadeInText';

class SplashScreen extends Component {

    constructor(props) {
        super(props)
        this.state = {
            enableChangeColor: false
        }
        // this.myTween = null;
        // this.myElement = null;
    }

    componentDidMount = () => {
        setTimeout(() => {
            this.setState({ enableChangeColor: true })
        }, 0)
        // this.myTween = TweenLite.to(this.myElement, 1, {x: 100, y: 100});
    }

    render() {
        let bgColor = this.state.enableChangeColor ? 'css-theme-color' : 'css-white-color'
        return (
            <section id="splash-bg" className={bgColor}>
                <h1 id="introTextH2">
                    <FadeInText textDelay={1000} animationType={'fadeInBottom'} innerText={'我是蘇桓晨，這是我收藏的作品集，您可以向下瀏覽，或是先。'}></FadeInText>
                </h1>
                {/* <h2 ref={h2 => this.myElement = h2} >123</h2> */}
            </section>
        )
    }

}

export default SplashScreen;
