import React, { Component } from 'react';
import './App.css';
import './SectionButton.scss';
import { Col, Button } from 'react-bootstrap';
import SVG from 'svg.js';

class SectionButton extends Component {

    constructor(props) {
        super(props)
        this.state = {
        }
    }

    componentDidMount = () => {
        this.drawSectionBtnIcon()
    }

    drawSectionBtnIcon = () => {
        let fBtnIconFrame = SVG('f-btn-icon').viewbox({ x: 0, y: 0, width: 25.127, height: 43.282});
        let fBtnIcon = fBtnIconFrame.path('M3.09,0.49l21.45,19.8c0.79,0.73,0.79,1.98,0,2.7L3.09,42.79C1.91,43.88,0,43.04,0,41.44V1.84C0,0.24,1.91-0.6,3.09,0.49z').fill('#353064');
        return fBtnIcon;
    }

    render() {
        let animationToggle = ''
        if (this.props.shouldButtonPresent === this.props.sectionName) {
            animationToggle = ' presenting'
        }   else {
            animationToggle = ' '
        }
        return (
            <Button variant="primary" className={'section-btn row mr-0 p-0 border-0 d-flex float-right flex-column justify-content-center' + animationToggle}>
                <h3 className={'en-xblod button-hint-top align-self-start' + animationToggle}>READ</h3>
                <Col sm={6} id={'f-btn-icon'} className={'d-flex p-2' + animationToggle}></Col>
                <h3 className={'cn-blod button-hint-bottom align-self-end' + animationToggle}>閱讀</h3>
            </Button>
        )
    }
}

export default SectionButton;