import React from 'react';
import './App.css';
import { Switch, Route, NavLink, BrowserRouter } from 'react-router-dom'
import FullPager from './FullPager';
import F from './F';
import Ai from './AI';
import Coe from './Coe';
import About from './About';
import SplashScreen from './SplashScreen';
import { TransitionGroup, Transition } from 'react-transition-group'
import play from './play'
import exit from './exit'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, Navbar, Nav, NavDropdown } from 'react-bootstrap';



function App() {
  return (

    <BrowserRouter basename="/structure-viability">
      <div className="app">
        <Nav />
        <Route render={({ location }) => {
          const { pathname, key } = location;

          return (
            [<Navbar bg="dark" expand="sm">
              <Navbar.Toggle aria-controls="basic-navbar-nav" />
              <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mr-auto"> 
                  <NavLink to="/splashscreen" activeClassName="selected">splashscreen</NavLink>
                  <NavLink to="/Feather" activeClassName="selected">Feather</NavLink>
                  <NavLink to="/About-Island" activeClassName="selected">About-Island</NavLink>
                  <NavLink to="/College-of-Education" activeClassName="selected">College-of-Education</NavLink>
                  <NavLink to="/About" activeClassName="selected">About</NavLink>
                  <NavLink exact to="/" activeClassName="selected">FullPager</NavLink>
                </Nav>
                <Button variant="primary">Primary</Button>
              </Navbar.Collapse>
            </Navbar>,
            <TransitionGroup component={null}>
              <Transition
                key={key}
                appear={true}
                onEnter={(node, appears) => play(pathname, node, appears, location)}
                onExit={(node, appears) => exit(node, appears)}
                timeout={{ enter: 750, exit: 750 }}
              >
                <Switch location={location}>
                  <Route path="/splashscreen" component={SplashScreen} />
                  <Route path="/Feather" component={F} />
                  <Route path="/About-Island" component={Ai} />
                  <Route path="/College-of-Education" component={Coe} />
                  <Route path="/About" component={About} />
                  <Route path="/" component={FullPager} />
                  <Route component={Coe} />
                </Switch>
              </Transition>
            </TransitionGroup>]
          )
        }} />
      </div>
    </BrowserRouter>
  )
}
export default App;
